<template>
    <div class="banner">
        <div class="swiper-container">
            <div class="swiper-wrapper">
                <div class="swiper-slide" v-for="(banner, index) in bannerList" :key="banner.id">
                    <img :src="banner.picUrl">
                </div>
            </div>
            <!--  -->
            <div class="swiper-pagination"></div>
        </div>
    </div>
</template>

<script>
// swipercss
import Swiper from 'swiper'
import 'swiper/dist/css/swiper.min.css'
export default {
    data: function() {
        return {
        }
    },
    props: {
        bannerList: {
            type: Array,
            default: function() {
                return []
            },
            require: true
        }
    },
    mounted() {
    },
    methods: {
        _initBanner() {
            this.$nextTick(() => {
                let banner = new Swiper('.swiper-container', {
                    // 
                    loop: true,
                    // 
                    pagination: {
                      el: '.swiper-pagination',
                    }
                })
            })
        }
    },
    components: {
    }
}
</script>
<style lang="scss">
.banner{
    .swiper-slide{
        img{
            width: 100%;
        }
    }
    
    .swiper-pagination-bullet-active{
        width: 15px;
        background: #fff;
        opacity: .3;
        border-radius: 5px;
    }
}
</style>