<template>
    <div class="rank-list">
        <div class="rank-list-tit">
            <h2></h2>
        </div>
        <div class="rank-list-con">
            <ul>
                <li v-for="(song, i) in songsList" :key="i" @click.stop="addPlayList(song)">
                    <router-link :to="'/play/'+song.data.songmid">
                        <div class="thumb">
                            <img v-lazy="'https://y.gtimg.cn/music/photo_new/T002R90x90M000'+ song.data.albummid +'.jpg?max_age=2592000'">
                        </div>
                        <div class="info">
                            <h3 class="song-name" v-text="song.data.singer[0].name"></h3>
                            <p class="singer-name" v-text="song.data.songname"></p>
                        </div>
                    </router-link>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import {mapMutations} from 'vuex'
export default {
    data: function() {
        return {
        }
    },
    props: {
        songsList: {
            type: Array,
            default: function() {
                return []
            },
            required: true
        }
    },
    methods: {
        ...mapMutations([
            'ADD_TO_SONG_LIST'
        ]),
        // ()
        addPlayList({data: {albummid, songmid, songname, singer}}) {
            this.ADD_TO_SONG_LIST({albummid, songmid, songname, singer})
            
        }
    },
    components: {
    }
}
</script>
<style scoped lang="scss">
.rank-list{
    &-con{
        li{
            padding: 0 10px;
            margin-bottom: 20px;
            a{
                display: flex;
            }
            .thumb{
                img{
                    width: 60px;
                    height: 60px;
                }
            }
            .info{
                margin-left: 20px;
                margin-top: 15px;
                h3{
                    color: #fff;
                    font-size: 15px;
                    margin-bottom: 5px;
                }
                p{
                    font-size: 12px;
                    color: #666;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
        }
    }
}
</style>