<template>
    <div class="home">
        <div id="scrollWrapper" :style="autoHeight">
            <div class="scroll-con">
                <Banner :bannerList="banner" ref="banner"></Banner>
                <RankList :songsList="rank.songlist"></RankList>
                <p class="loading" v-text="loadTips[curLoadTips]"></p>
            </div>
        </div>
    </div>
</template>

<script>
// 
import IScroll from 'iscroll/build/iscroll-probe'

// 
import API from '@/api/api'
import HTTP from '@/utils/http'

import Banner from 'COMPONENTS/home/Banner'
import RankList from 'COMPONENTS/home/RankList'
import {mapGetters} from 'vuex'

export default {
    name: 'home',
    data: function() {
        return {
            // 
            iscroll: null,
            // 
            banner: [],
            // 
            rank: {
                code: '',
                songlist: [],
                total_song_num: 0
            },
            // rank
            isFirstFetch: true,
            loadTips: {
                stp1: '',
                stp2: '',
                stp3: '...',
                stp4: ''
            },
            curLoadTips: 'stp1',
            // 
            offsetY: 30,
            // 
            isEndScroll: true
        }
    },
    computed: {
        ...mapGetters({
            curSong: 'getCurSong'
        }),
        // 
        hasMore: function() {
            return this.rank.songlist.length < this.rank.total_song_num
        },
        // miniplayer
        autoHeight: function() {
            return this.curSong ? 'height: calc(100vh - 85px - 60px)' : 'height: calc(100vh - 85px)'
        }
    },
    created() {
        this.getBannerList()
        this.updateRankList()
    },
    mounted() {
        setTimeout(() => {
            this.iscroll = new IScroll('#scrollWrapper', {
                mouseWheel: true,
                useTransform: true,
                useTransition: true,
                disableMouse: true,
                disablePointer: true,
                disableTouch: false,
                tap: false,
                click: true,
                probeType: 2
            })

            // 
            this.iscroll.on('scrollStart', () => {
                // 
                this.isEndScroll = false
            })
            // 
            this.iscroll.on('scroll', () => {
                if (!this.isFirstFetch && !this.hasMore) {
                    this.curLoadTips = 'stp4'
                    return this.loadTips['stp4']
                }

                if(Math.abs(this.iscroll.maxScrollY) + this.offsetY <= Math.abs(this.iscroll.y) && !this.isEndScroll) {
                    this.curLoadTips = 'stp2'
                } else {
                    this.curLoadTips = 'stp1'
                }
            })
            // 
            this.iscroll.on('scrollEnd', () => {
                if(this.iscroll.maxScrollY >= this.iscroll.y) {
                    this.updateRankList()
                }

                this.iscroll.scrollTo(0, this.iscroll.y + this.offsetY);
                
                // 
                this.isEndScroll = true
            })
        }, 500);
    },
    methods: {
        // banner
        getBannerList() {
            HTTP.get(API.home.banner, {param: 'jsonpCallback'})
            .then(({code, data: {slider}}) => {
                this.banner = slider
                this.$refs.banner._initBanner()
            })
        },
        // 
        updateRankList() {
            let beginNum = 0;
            try{
                this.iscroll.refresh()
            } catch(e) {
            }
            if (!this.isFirstFetch) {
                beginNum = this.rank.songlist.length
            }

            // 
            if(!this.isFirstFetch && !this.hasMore) {
                this.curLoadTips = 'stp4'
                return this.loadTips['stp4']
            } 


            this.curLoadTips = 'stp3';
            HTTP.get(`${API.home.rank}&song_begin=${beginNum}`, {param: 'jsonpCallback'})
            .then(({code, songlist, total_song_num}) => {
                if (this.isFirstFetch) {
                    this.rank = {code, songlist, total_song_num}
                    // false
                    this.isFirstFetch = false
                } else {
                    this.rank.songlist = this.rank.songlist.concat(songlist)
                }
                this.curLoadTips = 'stp1';
            })
        },
        calcDateStr() {
            let timeStamp = new Date()
            let year = timeStamp.getFullYear()
            let month = timeStamp.getMonth() + 1
            let day = timeStamp.getDate()
            month = month > 10 ? month : `0${month}`
            return `${year}-${month}-${day}`
        }
    },
    components: {
        Banner,
        RankList
    }
}
</script>
<style scoped lang="scss">
@import 'SCSS/index';
</style>
